/* ABOUT */
// { top: 0;left: 0;width: 100%;transition: 0.8s transform cubic-bezier(0.65, 0, 0.35, 1); z-index: -1; background-color: #FFF; padding-top: 90px; padding-top: 20vh;}

.about { 
    main span{display: inline;}
    a:link:active, a:visited:active {color: #000;}
    .main {width: 100vw; margin: 0px; overflow: auto;}

    top: 0;left: 0;width: 100%;transition: 0.8s transform cubic-bezier(0.65, 0, 0.35, 1); z-index: -1;padding-top: 90px; padding-top: 20vh;
    height: 100vh; -webkit-transition: background-color 500ms ease-out;-moz-transition: background-color 500ms ease-out; -o-transition: background-color 500ms ease-out;transition: background-color 500ms ease-out;position: fixed;
    h1 em{display: inline-block; font-style: normal; transition: none;}
    
}



/* PROJECT */
.project { 
    padding: 250px 0 100px;
    .sub-cnt{padding: 60px 70px 10px;}
    .sub-cnt p {text-align: left; font-size: 1.8rem; margin: 0;}
    .sub-cnt.right p {text-align: right;}
    .sub-cnt .red-line {align-items: flex-start;}
    
    .sub-cnt.right .red-line {align-items: flex-end;}
    .red-line{display: flex;flex-direction: column;align-items: center;}
    .red-line h1 {text-align: center; font-size: 4rem; z-index: 1;}
    .red-line h2 {text-align: left; font-size: 4rem; z-index: 1;}
    .red-line >span{position: absolute;display: inline-block; opacity: 0.4; transform: rotate(-1deg); z-index: 0; }
    .red-line>h1+span{width: 750px; margin-top: 90px;}
    .red-line>h2+span{width: 350px; margin-top: 80px;margin-left: -20px;}
    .red-line h1 em{display: inline;}
    
    .sub-cnt .skill{display: flex; padding: 40px 0px 20px;flex-wrap : wrap; vertical-align: middle; align-content: center;}
    .sub-cnt.right .skill{justify-content: flex-end;}
    .section-cont .post .content h4{font-size: 2rem;}
    .skill img{height: 90px; width: 90px;}
    .skill h4{display: inline; vertical-align: middle; font-size: 1.2rem; margin: auto 0px;}
    
    .center .slick-list{margin-top: 10px;z-index: 3; }
    .center .slick-track{justify-items: center;display: flex; margin: auto;}
    .center .slick-slide{
        margin: 20px;transition: all 500ms ease;max-width: 500px;border-radius: 10px;
        &.slick-active {opacity: 1;}
        img{object-fit: cover; height: 100%; width: 100%;height: 250px;border-radius: 10px;}        
        div{width: 100%; height: 100%;}
        .proj{position: relative; margin: 0px;}
        .proj a{text-decoration: none;}
        .proj img{position: absolute; top: 0; left: 0; width: 100%; height: 100%;}
        .proj .open{width: 100vw; height: 100vh;}
        .proj-title{z-index: 30;position: relative;padding: 20px 0px; width: 100%;}
        .proj-title h1{font-size: 1.4rem; text-align: center;}
        .proj-title h2{font-size: 1rem; text-align: center;}
        .proj-title h3{font-size: 0.6rem; text-align: center; line-height: 2;}
        // .center .slick-slide:hover{transform: scale(1.1);}
    }
    .square {z-index: 100; position: absolute;width: 100%;height: 100%;cursor: pointer;}
    // .full-screen-square{position: fixed;top: 0;left: 0;z-index:10; width: 100vw;height: 100vh; cursor: pointer;background-color: #fff;}
    // .detail-container{z-index: 11; position: relative;top: 0;left: 0;}
    // .detail-container .content{z-index: 20;}
    .proj-title .skill{display: inline-flex; padding: 0px;flex-wrap : wrap;}
    .center .slick-prev{display: none !important;}
    .center .slick-next{display: none !important;}
    &.detail-active{z-index: -1; pointer-events: none;}
    
    .dots{position: absolute;display: block;width: 100%; padding: 0;margin: 0;list-style: none;text-align: center;}
    // .dots{display: inline; justify-content: center; margin-top: 20px; list-style-type: none;}
    .dots li{position: relative;display: inline-block;margin: 0 5px;padding: 0;width: 20px;height: 20px;cursor: pointer;}
    .dots li button{margin-right: 20px; width: 10px !important; height: 7px !important; border: 0;text-indent:-10000px; cursor: pointer;}
}


@media (max-width:1200px) {
    .center .slick-slide .proj-title h1{font-size: 1.2rem;}
    .project .center .slick-slide .proj-title h2{font-size: 0.8rem;}
    .project .skill img {height: 60px;width: 60px;}
    .project .skill h4 {font-size: 1.5rem;}
}

@media (max-width:1024px) {
    .project .section-cont .post h2{font-size: 2.5rem;}
    .project .section-cont .post h3{font-size: 2.8rem;}
    .project .skill img {height: 50px;width: 50px;margin-right: 10px;}
    .project .skill h4 {font-size: 1.2rem;}
}

/* iPad Pro 12 */
@media (max-width:1024px) and (min-height:1200px) {
    .about{padding-top: 25vh;}
    .proj{padding-top: 25vh;}
}

@media (max-width:900px) {
    .project .center.backend .slick-slide{opacity: 1;}
    .about h1 {font-size: 2.7rem; width: 230px;}
    .about h1 em{font-size: 2.7rem;}
    .about .switch {max-width: 600px;}
    .about h2 {font-size: 2.2rem;width: 230px;}
    .about h2 em{font-size: 2.2rem;}
}

@media (max-width:800px) {
    .project .sub-cnt p {font-size: 1.3rem;}
    .project .red-line h1 {font-size: 3rem;}
    .project .red-line>h1+span {width: 500px;margin-top: 70px;border-width: 17px}
}

@media (max-width:660px) {
    .break-line660 {display: block;}
    .about h2 {font-size: 2rem;}
    .project .section-cont .post h2{font-size: 1.6rem;}
    .project .section-cont .post h3{font-size: 2rem;}
    .project .sub-cnt {padding: 60px 30px 10px;}
    .project .red-line h1 em{display: none;}
    .project .red-line>h1 { font-size: 4rem;}
    .project .red-line>h1 span{font-size: 2rem;}
    .project .red-line>h1+span {width: 400px;margin-top: 130px;border-width: 30px;transform: rotate(-5deg);}
}

@media (max-width:550px) {
    .about .inner{padding-top: 90px;}
    .about .switch{flex-direction: column;}
    .about h1 {font-size: 3rem;margin-top: 40px;margin-bottom: 20px;}
    .about .link.switch {padding-top: 70px; padding-bottom: 20px;}
    .about .switch {padding-top: 10px;}
    .about .sub-title h1 {font-size: 3.4rem;}
    .about .sub-title h3 {font-size: 1.6rem;margin-top: 30px;}
    .about .sub-title:last-child {padding-top: 0px;}
    .about .sub-title h1 {margin: 0px;}
    .about .sub-title h1+span {width: 250px;margin-top: 45px;}
    .about .sub-title h3+span {width: 350px;margin-top: 50px;}
    .project .center .slick-slide .proj-title{margin: 40px auto;}
    .project .center .slick-slide .proj-title h2{font-size: 0.8rem; }
    .project .center .slick-slide .proj-title h1{font-size: 1.5rem; }
    .project .center .slick-slide .proj-title h3{font-size: 0.8rem;margin-top: 30px;}
    .project .section-cont .post h2{font-size: 1.2rem;}
    .project .section-cont .post h3{font-size: 1.5rem;}
    .project .red-line h2 {font-size: 2.8rem;}
    .project .red-line>h2+span {width: 240px;margin-top: 70px;}
}

@media (max-width:480px) {
    .inner{padding-top: 90px;}
    .project .center .slick-slide .proj-title h1{font-size: 1.2rem; }
    .center .slick-slide{margin: 0px;}
    .project .section-cont .post h2{font-size: 1.2rem; margin: 40vh 0px 30px 5vw;}
    .project .section-cont .post h3{font-size: 1.5rem;margin-left: 5vw;}
    .about h2 {font-size: 2rem;}
    .about .switch img {width: 70px;}
    .project .red-line>h1 { font-size: 3rem;}
    .project .red-line>h1 span{font-size: 1.5rem;}
    .project .red-line>h1+span {width: 300px;margin-top: 100px;border-width: 15px;transform: rotate(-3deg);}
}

@media (max-width:375px) {
    .about .inner{padding-top: 80px;}
    .about .sub-title h3 {font-size: 1.4rem;}
    .about .sub-title h3+span {width: 320px;margin-top: 50px;}
    .about .sub-title h1 {margin: 0px 0px 30px;}
    .project .red-line h2 {font-size: 3rem;}
    .project .red-line>h2+span {width: 250px;margin-top: 80px;}
}



